@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


$primary-color: #AA0601;
$secondary-color: #FBF2F2;
$tertiary-color: #111619;

$primary-font-color: #650300;

$header-height: 108px;
$tablet-header-height: 108px;
$mobile-header-height: 108px;

$footer-height: 108px;
$tablet-footer-height: 108px;
$mobile-footer-height: 108px;


html,body{
	width: 100%;
	height: 100%;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	font-size: 16px;
	line-height: 24px;
	font-family: 'Noto Sans', Roboto, "Helvetica Neue", Arial, Helvetica, sans-serif;
	color: $primary-font-color;
}

.eng{
	font-family:  Roboto,'Noto Sans', "Helvetica Neue", Arial, Helvetica, sans-serif;
}

h1{
	font-size: 64px;
	line-height: 96px;
	font-weight: bold;
}

h2{
	font-size: 48px;
	line-height: 72px;
	font-weight: bold;
}

h3{
	font-size: 24px;
	line-height: 36px;
	font-weight: bold;
}

a {
	text-decoration: none;
}


.header {
  background-color: $primary-color;
  padding: 30px 88px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: $header-height;

  .logo {
  	width: 80px;
  	height: 49px;
  }

  .menu .menu-item {
  	margin: 0 32px;
  	float: left;
  	a {
  		color: white;
  		font-size: 20px;
  	}
  }

}


.container{
	width: 1110px;
	margin: 0 auto;
	// background-color:black;
}

.flex-row{
	display: flex;
	flex-direction: row;
}

.flex-row.reverse{
	flex-direction: row-reverse;
}

.flex-column{
	display: flex;
	flex-direction: column;
}

.flex-column.reverse{
	flex-direction: column-reverse;
}

.justify-content-center{
	justify-content: center;
}

.align-item-center{
	align-items: center;
}


///////////////
// index
///////////////

.banner{
	width: 100%;
	height: calc( 100% - #{$header-height} );
	background-image: url('../images/banner.png');
	background-position: center;
	background-repeat: no-repeat;
	
	.container{
		position:relative;
	}
	.banner-text{
		text-align: right;
		
		display: inline;
		position: absolute;
		top: 348px;
		right: 0;
		.banner-subtitle{
			font-size: 36px;
			line-height: 54px;
		}
	}
}

.profession{
	background: #AA06010D;
	padding: 60px 0 80px 0;
	display: flex;
	flex-direction: column;
	align-items: center;

	.container{
		display: flex;
		justify-content: space-between;
		margin-top: 40px;

		.content-div{
			max-width: 255px;
			text-align: center;

			.icon{
				width: 100px;
				height: 100px;
				margin-bottom: 16px;
			}

			.title{
				margin-bottom: 8px;
			}

			.desc{
				color: black;
				text-align: left;
			}
		}
	}
}

.classic{
	padding: 60px 0 80px 0;
	text-align: center;
	$pic-width: 57.21%;

	.container{
		margin-top: 40px;
		.flex-row{
			margin-bottom: 32px;
			
			.pic-div{
				width:$pic-width;

				img{
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			.title-div{
				width: calc( 100% - #{$pic-width} );
				display: flex;
				justify-content: flex-start;
				align-items: center;
				padding: 32px;
				
				h3{
					color: #AA0601;
					font-size: 48px;
					font-style: italic;
					text-transform: uppercase;
					line-height: 72px;

					display: flex;
					align-items: center;
					img{
						width: 48px;
						height: 48px;
						margin-right: 10px;
					}
				}
			}
		}

		.reverse .title-div{
			justify-content: flex-end;
		}

	}

}

.design{
	padding: 60px 0 80px 0;
	text-align: center;
	background: url('../images/bg.png');
	background-position: 0 center;
	background-repeat: no-repeat;
	background-attachment: fixed;

	h2{
		color: #fff;
	}

	.container{
		margin-top: 40px;
	}

	.brand{
		width: 540px;
		height: 540px;
		display: flex;
		justify-content: center;
		align-items: center;

		h3{
			font-size: 72px;
			line-height: 108px;
		}
	}
	.brand.double-a{
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url('../images/product-8.png');
		margin-right: 30px;
	}

	.brand.youth{
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url('../images/product-9.png');
	}
}

.testimonial{
	padding: 60px 0 80px 0;
	text-align: center;

	.container{
		margin-top: 40px;
		justify-content: space-between;

		.block{
			max-width: 255px;
			border: 1px solid #DEE2E6;
			box-shadow: 0px 2px 6px #00000029;
			border-radius: 4px;
			img{
				width:100%;
				height: 256px;
				object-fit: cover;
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
			}

			.text{
				height: 204px;
				text-align: left;
				padding: 16px;
				position: relative;
				border-bottom-left-radius: 4px;
				border-bottom-right-radius: 4px;
				p{
					font-size: 14px;
					line-height: 21px;
					color: #212529;
					
				}

				.name{
					font-weight: bold;
				}

				.desc{
					margin-top: 8px;
					font-size: 16px;
					line-height: 24px;
				}

				.date{
					color: #707070;
					position: absolute;
					bottom: 16px;
				}
			}
		}
	}

	
}

.contact{
	background: #AA06010D;
	padding: 60px 0 80px 0;
	text-align: center;
	
	h2{
		color: #000;
	}

	.container{
		width: 730px;
		margin-top: 40px;
		text-align: left;
		
		.form-desc{
			color: #000;
			font-size: 20px;
			line-height: 30px;
			margin-bottom: 20px;
		}

		form{
			label,input,textarea{
				display: block;
				font-size: 16px;
				line-height: 24px;
			}

			label{
				color: #000;
				cursor: pointer;
			}

			input:not([type=checkbox]),textarea{
				width: 100%;
				// height:50px;
				border-radius: 0;
				border: 1px solid #000;
				padding: 14px 0 14px 16px;
				background: transparent;
				&::placeholder{
					opacity: 0.2;
				}
			}

			textarea{
				height: 136px;
				resize: none;
			}

			.form-row{
				margin-bottom: 16px;
			}

			#check-submit{
				width: 24px;
				height: 24px;
				margin-right: 4px;
			}

			input[type=submit]{
				width: 350px;
				color: #fff;
				margin: 40px auto 0 auto;
				background: #AA0601;
				border: unset;
				cursor: pointer;
			}

		}
	}
}


.footer {
	background-color: $primary-color;
	padding:32px 0;

	.footer-1{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 24px;

		.menu .menu-item {
	  	float: left;
	  	margin-right:48px;
	  	a {
	  		color: white;
	  		font-size: 16px;
	  		line-height: 24px;
	  	}
	  }

	  .social .social-item{
	  	float: left;
	  	margin-left: 32px;

	  	.social-icon{
	  		width:48px;
	  		height:48px;
	  	}
	  }
	}

	.footer-2,.footer-3{
		margin-bottom: 24px;
		
		img{
			width: 36px;
			height: 36px;
			margin-right: 20px;
		}

		a{
			color: white;
			font-size: 36px;
			line-height: 54px;
		}
	}

	.hr{
		height: 1px;
		border-top: 1px solid #fff;
		margin-bottom: 32px;
	}

	.footer-4{
		display:  flex;
		justify-content:  space-between;

		.copyright{
			color: white;
			font-size: 16px;
			line-height: 24px;
		}

		.privacy-policy{
			color:  white;
			margin-left:auto;
			margin-right: 48px;
		}

		.privacy-policy,.services{
			color:  white;
			font-size: 14px;
			line-height: 21px;
		}
	}
}



